<template>
    <div id='box'>
      <div class="top">
        <div @click="addressShow=true" v-if="!isaddress">
          <van-cell title="请选择收货地址" is-link/>
        </div>
        <div class="res" @click="addressShow=true" v-else>
          <div>收货人：{{addresinfo.consignee}}</div>
          <div>收货地址：{{addresinfo.province + ' ' + addresinfo.city + ' ' + addresinfo.district}}</div>
          <div>联系方式：{{addresinfo.mobile}}</div>
          <span class="pos">点击切换</span>
        </div>
      </div>
      <img src="@/assets/images/my/address.png" alt="">

      <div class="goods" v-for="(item,index) in goodsdata" :key="index">
        <img :src="item.original_img" alt="" class="img">
        <div class="right">
          <p>{{item.goods_name}}</p>
          <div class="taocao">{{item.spec_key_name}}</div>
          <p><span class="price">￥{{item.shop_price}} </span><span class="num">x{{item.goods_num}}</span></p>
        </div>
      </div>
      <div class="count">小计：<span>￥{{count.toFixed(2)}}</span></div>
      <div class="beizhu">
        <van-field
          v-model="user_note"
          label="订单备注："
          type="textarea"
          placeholder="请输入备注内容"
          rows="1"
          autosize
        />
      </div>
      <div class="other">
        <van-cell title="运费">
          <span style="color:red">{{Number($route.query.freight)>0?'+￥'+$route.query.freight:'免运费'}}</span>
        </van-cell>
        <!-- <van-cell title="会员折扣" value="无会员折扣" /> -->
        <van-cell title="优惠券" value="无可用优惠券" />
      </div>
      <div class="bottom">
        <p>合计：<span style="color:#FE7250;font-weight:600">￥{{(Number(count)+Number($route.query.freight)).toFixed(2)}}</span></p>
        <button class="submit" @click="submit()">去付款</button>
      </div>
      <van-popup style="width:100%;height:100%"
        v-model="addressShow"
        position="right"
        :overlay="false"
        class="address_popup">
        <address1></address1>
      </van-popup>
    </div>
</template>
<script>
import { Cell, Field, Popup, Toast  } from 'vant';
import { shouhuodizhi, addOrder, submit_order } from '@/axios/getData';
import  address1 from '@/components/my/shouhuodizhi.vue'
export default {
    components: {
      vanCell: Cell,
      vanField: Field,
      vanPopup: Popup,
      address1,
    },
    props: {

    },
    data() {
        return {
          addressShow:false,
          isaddress:false,    // 
          user_note:'',       // 订单备注
          data22:[],          // 收货地址
          addresinfo:{
            address_id:'',  //  地址id
          },    // 收货地址信息
          goodsdata:[],   //  商品信息  
          count:0,    // 总价
          fukuan:false,   //  提交订单开关   避免反应慢多次提交
        };
    },
    computed: {

    },
    async created() {
      //  购物车跳转
      if(!this.$route.query.action){
        console.log(JSON.parse(this.$route.query.item));
        var item = JSON.parse(this.$route.query.item);
        var arr = [];
        item.forEach(ele => {
          arr.push(ele.id)
        });
        var data = {}
        data.action = this.$route.query.action;
        // data.goods_id = this.$route.query.goods_id;
        // data.goods_num = this.$route.query.goods_num;
        data.cart_ids = arr.join(',');
        console.log(data)
        var res = await submit_order(data);
        console.log(res)
        this.goodsdata = res.data.cartList
        for(var i=0;i<res.data.cartList.length;i++){
          this.count = Number(this.count)+(Number(res.data.cartList[i].shop_price)*Number(res.data.cartList[i].goods_num))
        }

        return;
      }


      var data = {}
      data.action = this.$route.query.action;
      data.goods_id = this.$route.query.goods_id;
      data.goods_num = this.$route.query.goods_num;
      data.item_id = this.$route.query.item_id;
      console.log(data)
      var res = await submit_order(data);
      this.goodsdata = res.data.cartList
      console.log(this.goodsdata, 777)
      // return;
      for(var i=0;i<res.data.cartList.length;i++){
        this.count = Number(this.count)+(Number(Number(res.data.cartList[i].shop_price)*Number(res.data.cartList[i].goods_num)))
      }
      // console.log(typeof this.count)
    },
    async mounted() {
      var res = await shouhuodizhi()
      this.data22 = res.data;
      if(res.data.length){
        this.addresinfo = res.data[0];
        this.isaddress = true
      }
      var that = this;
      that.$hu.$on('getaddress',function(val){
        that.addressShow = false;
        that.isaddress = true
        that.addresinfo = val;
        console.log(that.addresinfo)
      })
    },
    methods: {
      async submit(){
        if(!this.addresinfo.address_id){
          Toast('请选择收货地址')
          return;
        }
        if(this.fukuan){
          Toast('订单已提交...')
          return;
        }
        this.fukuan = true;   //  付款开关避免多次提交订单
        if(!this.$route.query.action){    //   购物车跳转
          var item = JSON.parse(this.$route.query.item);
          var arr = [];
          item.forEach(ele => {
            arr.push(ele.id)
          });
          var data = {}
          data.action = this.$route.query.action;
          data.cart_ids = arr.join(',');
          data.user_note = this.user_note;
          data.address_id = this.addresinfo.address_id;
          addOrder(data).then((res)=>{
            if(res.code==200){
              this.onBridgeReady(res)
            }
          });
          return;
        }

        var that = this;
        var data = {};
        data.action = this.$route.query.action;
        data.goods_id = this.$route.query.goods_id;
        data.goods_num = this.$route.query.goods_num;
        data.item_id = this.$route.query.item_id;
        data.user_note = this.user_note;
        data.address_id = this.addresinfo.address_id;
        addOrder(data).then((res)=>{
          if(res.code==200){
            that.onBridgeReady(res)
          }
        });
      },
      onBridgeReady(res) {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: res.data.appId, //公众号名称，由商户传入
            timeStamp: res.data.timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: res.data.nonceStr, //随机串
            package: res.data.package,
            signType: res.data.signType, //微信签名方式：
            paySign: res.data.paySign //微信签名
          },
          res => {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              this.$router.push({ path: "/success" });
            }
            if (res.err_msg == "get_brand_wcpay_request:cancel") {
              Toast('支付失败')
              // this.$router.push('/')
            }
          }
       );
     }
    },
};
</script>

<style scoped lang="scss">
#box{
  height:100vh;
  background: #F5F3F3;
  .top{
    padding:5pt 10pt;
    background: #fff;
  }
}
.goods{
  position: relative;
  display: flex;
  padding:15pt;
  background: #fff;
  font-size: 13pt;
  .right{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    .num{
      color:#999;
      font-size: 11pt;
    }
  }
  .img{
    margin-right:15pt;
    width:70pt;
    height:70pt;
  }
  .price{
    display: inline-block;
    color:#FF4C24;
    font-weight: 600;
    margin-right:15pt;
    font-size: 15pt;
  }
  .taocao{
    color:#999;
    margin:3pt 0;
    font-size: 7pt;
  }
}
.goods::after{
  position: absolute;
  content:'';
  display:block;
  background: #DEDEDE;
  width:90%;
  bottom:0;
  height:1px;
}
/* 小计 */
.count{
  text-align: right;
  background: #fff;
  padding-bottom: 5pt;
  padding-right:20pt;
  &>span{
    font-size: 13pt;
    color:#FF4C24;
    font-weight: 600;
  }
}

/* 备注 */
.beizhu{
  margin:5pt 0;
}

/* 其他 */
.other{
  width:100vw;
  background: #fff;
  padding:5pt;
  box-sizing: border-box;
}

/* 底部 */
.bottom{
  font-size: 14pt;  
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  bottom:0;
  height:40pt;
  line-height: 40pt;
  padding:0 30pt;
  box-sizing: border-box;
  background: #fff;
  width:100%;
  .submit{
    width:35%;
    height:30pt;
    line-height: 30pt;
    background: #FF4C24;
    border:0;
    border-radius: 15pt;
    color:#fff;

  }
}

/* 收货地址 */
.res{
  position: relative;
  .pos{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    right:5%;
    color:#ddd;
  }
}
</style>



